.veilarbmaofs {
	scroll-behavior: smooth !important;
	flex-grow: 1;
	width: 100%;

	background-color: #f4f4f4;

	.ReactCollapse--collapse {
		transition: none;
		height: auto;
	}

	color: var(--a-gray-900);
	font-family: var(--a-font-family);

	&__container {
		max-width: 1920px;
		min-height: 30vh;
		margin: 0.75rem auto 0;

		display: flex;
		align-items: flex-start;
		-moz-box-flex: 1;
	}

	&--hidden {
		// Overskriv styling fra veilarbpersonflatefs
		display: none !important;
	}

	.informasjonsbolk {
		margin: 1rem 0 1rem 0;
	}

	.informasjonsbolk-tittel {
		display: flex;
		align-items: center;
		gap: 0.5rem;
		margin-bottom: var(--a-spacing-1);
	}

	.underinformasjon {
		margin-bottom: 1rem;
	}

	.overinformasjon {
		margin-top: 0.2rem;
	}

	.innrykk {
		padding-left: 0.5rem;
	}

	.italic {
		font-style: italic;
	}

	.italic-gra {
		color: var(--a-gray-600);
		font-style: italic;
	}

	.wrap-anywhere {
		overflow-wrap: anywhere;
	}

	.flex-align-center {
		display: flex;
		align-items: center;
	}

	.alertstripe_intern {
		padding: 1rem;

		.navds-body-long {
			line-height: 1.375rem;
			font-size: 1rem;
		}
	}
}
